import React, { Component } from 'react'
import $ from 'jquery'
import './Skill.scss'
import c from '../img/c++.png'
import java from '../img/java.png'
import linux from '../img/linux.png'
import python from '../img/python.png'

import android_studio from '../img/android.png'
import react_ from '../img/react.png'

export default class Skill extends Component {
    state ={
        img_language : [
            ["C/C++",c],
            ["Java",java],
            ["Php", "https://upload.wikimedia.org/wikipedia/commons/2/27/PHP-logo.svg"],
            ["Python",python],
        ],
        img_extra_skill : [
            ["Linux",linux],
            ["Mikrotik","https://designs.mikrotik.com/images/large/mikrotik.png"]
        ],
        img_other : [
            ["Android Studio",android_studio],
            ["React",react_],
            ["Visual Studio","https://upload.wikimedia.org/wikipedia/commons/thumb/e/e4/Visual_Studio_2013_Logo.svg/580px-Visual_Studio_2013_Logo.svg.png"],
        ]

    }
    render() {
        var other = <div className="container_img_other">
            {
                this.state.img_other.map((temp) =>
                    <div className="image_border">
                        <div className="img_other__"><img async src={temp[1]} /></div>
                    </div>
                )
            }
        </div>
        var network = <div className="container_img_network">
            {
                this.state.img_extra_skill.map((temp) =>
                    <div className="image_border">
                        <div className="img_network__"><img async src={temp[1]} /></div>
                    </div>
                )
            }
        </div>
        var languages = <div className="container_img_language">
            {
                this.state.img_language.map((temp) =>
                    <div className="image_border">
                        <div className="img_language__"><img async src={temp[1]} /></div>
                    </div>
                )
            }
        </div>
        return (
            <div className="skill__">
                <div className="text_skill one">
                    Since Vocational High School i have praticed networking.
                </div>
                <div className="img_network">
                    {network}
                </div>
                <div className="text_skill">
                    Now after working in Lab Assistant now have mastered several programming languages.
                </div>
                <div className="img_language">
                    {languages}
                </div>
                <div className="text_skill">
                    Other Skill
                </div>
                <div className="img_other">
                    {other}
                </div>

            </div>
        )
    }
}
