import React, { Component } from 'react'
import './Bar.scss'
import $ from 'jquery'
export default class componentName extends Component {
    render() {
      function home(){
        $('*').animate({scrollTop: 0}, 1000)
      }
      function about(){
        let body_height = document.body.scrollHeight
        $('*').animate({scrollTop: body_height}, 1000)
      }
      function skill(){
        let body_height = document.body.scrollHeight
        let profile_height = $('.myprofile_').height()
        $('*').animate({scrollTop: body_height+profile_height}, 1000)
      }
        return (
          <div className='bar_home_'>
            <span id="bar_home" onClick={home}>Home</span>
            <span id="bar_about" onClick={about}>About</span>
            <span id="bar_skill" onClick={skill}>Skill</span>
          </div>
        )
    }
}
