import React, { Component } from 'react'
import './Home.scss'
import $ from 'jquery'
import Typical from 'react-typical'
import Typist from 'react-typist'


import Typewriter from 'typewriter-effect';


export default class Home extends Component {
  state = {
    script__ : [
      "var myName = \"Hello my name is zi\"",
      "/*",
      "simple code",
      "*/",
      "",
      "for(var i = 0; i < 1000; i++){",
      "var print = myName + \", I'm a programmer\"",
      "console.log(print)",
      "}",
      "",
      "/*",
      "this code will \"print log Hello my name is zi, i'm a programmer\" a 1000 times",
      "*/",
    ],
    idx: 1
  }
  render () {
    $().ready(function(){
      $("#script__").slideDown(1000)
    })
    return (
      <div id='bg_parent'>
        
        <div className='border_home_'>
          <article className='my_name'>Hi, I'm <span>ZI</span></article>
          <span className='i_m'>
              I'm a{' '}
              <span>
            <Typical
              loop={Infinity}
              wrapper='b'
              steps={[
                'Blogger',
                2000,
                'Designer',
                2000,
                'Developer',
                2000,
                'Reverse Engineer',
                2000
              ]}
              />
              </span>
          </span>
        </div>
        <div id='script__'>
          <div className="btn_text__">
            <div className="minimize"></div>
            <div className="maximize"></div>
            <div className="exit"></div>
          </div>
              <Typist>
                {this.state.script__.map((text) => 
                  <span>
                    {'| '+ text}
                    <Typist.Delay ms={200} />
                    <br/>
                  </span>
                )}
              </Typist>
        </div>

      </div>
    )
  }
}
